.Homepage {
    background-image: url(../backgrounds/image/home.jpg);
    
}
img {
    min-height: 100%;
    min-width: 800px; 
    height: auto;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0; 
    -webkit-filter: opacity(.7);
    filter: opacity(.7);  
} 
.container {
    position: relative;
    text-align: center;
    color: white;
  }

.centered {
    position: absolute;
    margin-top: 5em;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-style: oblique;
  }

